body {
  display: block;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}
.page-header .container,
.page-header {
  width: 320px;
  padding: 0;
}
.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 260px;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  src: url("fonts/opensans.woff2") format("woff2"), url("fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  src: url("fonts/opensansbold.woff2") format("woff2"), url("fonts/opensansbold.woff") format("woff");
}
@media (min-width: 768px) {
  .page-header {
    width: 768px;
  }
  .page-header .container,
  .container {
    padding: 0 50px;
    width: calc(768px - 100px);
  }
}
@media (min-width: 1150px) {
  .page-header {
    width: 1150px;
  }
  .page-header .container,
  .container {
    padding: 0 50px;
    width: calc(1150px - 100px);
  }
}
.page-header {
  display: flex;
  width: 100%;
  flex-direction: column;
  border-top: 1px solid #ececec;
}
.page-header__logo {
  padding: 20px 0px;
  display: flex;
  margin: auto;
  width: 86px;
  height: 35px;
}
.page-header__logo:hover {
  opacity: 0.5;
}
.page-header__logo:active {
  opacity: 0.2;
}
.main-nav--closed .site-list,
.main-nav--closed .user-list {
  display: none;
}
.main-nav--nojs,
.main-nav--opened {
  display: block;
}
.nav-toggle {
  display: flex;
  border: none;
  background: #ffffff;
  padding: 31px 30px 48px;
  height: 14px;
  margin: -80px 0 0 240px;
}
.main-nav--closed .nav-toggle::before {
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
}
.main-nav--opened .nav-toggle {
  padding: 37px 25px 42px;
}
.main-nav--opened .nav-toggle::before,
.main-nav--opened .nav-toggle::after {
  content: "";
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .nav-toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .nav-toggle::after {
  margin: 0 0 0 -30px;
  transform: rotate(-45deg);
}
.main-nav--nojs .nav-toggle {
  display: none;
}
.main-nav--closed .page-header__logo,
.main-nav--closed .page-header__nav-toggle {
  display: flex;
}
.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
.site-list,
.user-list,
.offer-list {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.nav-note {
  font-size: 15px;
  line-height: 24px;
  font-weight: normal;
  color: #444444;
}
.offer-list,
.nav-note {
  display: none;
}
.site-list__item,
.user-list__item {
  border-top: 1px solid #ececec;
}
.site-list__item a,
.user-list__item a,
.offer-list__item a {
  display: flex;
  padding: 26px 0 28px 75px;
  text-decoration: none;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
  cursor: pointer;
}
.site-list__item a:hover,
.user-list__item a:hover,
.offer-list__item a:hover {
  color: #8d8d8d;
}
.site-list__item a:active,
.user-list__item a:active,
.offer-list__item a:active {
  color: #cacaca;
}
.user-list__item a {
  padding: 32px 0 34px 83px;
}
.user-list__search svg {
  margin-left: -49px;
  margin-top: 6px;
  position: absolute;
  fill: #d3d2d2;
}
.user-list__search:hover svg {
  fill: #9ce2d4;
}
.user-list__search:active svg {
  fill: #d7f3ee;
}
.user-list__cart svg {
  margin-left: -51px;
  margin-top: 4px;
  position: absolute;
  fill: #d3d2d2;
}
.user-list__cart:hover svg {
  fill: #9ce2d4;
}
.user-list__cart:active svg {
  fill: #d7f3ee;
}
@media (min-width: 768px) {
  .main-nav {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
  .page-header__logo {
    padding: 0;
    margin: 25px 0 0;
  }
  .page-header__logo--img {
    width: 113px;
    height: 46px;
  }
  .page-header {
    flex-direction: row;
  }
  .main-nav .nav-toggle {
    display: none;
  }
  .main-nav .site-list,
  .main-nav .user-list {
    display: flex;
    flex-direction: row;
    flex-grow: 0;
  }
  .user-list {
    order: 1;
    flex-grow: 1;
    min-height: 90px;
  }
  .site-list {
    order: 2;
  }
  .user-list span {
    display: none;
  }
  .user-list__item {
    border: none;
    border-left: 1px solid #ececec;
  }
  .site-list__item {
    border: none;
  }
  .site-list__item a {
    padding: 0;
    margin: 17px 35px 25px 0;
    font-size: 15px;
  }
  .nav-note {
    order: 3;
    display: flex;
    padding: 2px 0 0;
  }
}
@media (min-width: 1150px) {
  .site-list {
    order: 1;
    min-width: 400px;
  }
  .site-list__item a {
    margin: 30px 35px 25px 0;
    font-size: 17px;
  }
  .site-list__item:last-child a {
    margin: 30px 0 25px 35px;
  }
  .page-header__logo {
    order: 2;
    margin: 10px auto 0;
    width: auto;
    height: auto;
  }
  .page-header__logo--img {
    width: 148px;
    height: 73px;
  }
  .user-list {
    padding: 0 0 0 50px;
    min-width: 350px;
    order: 3;
  }
  .user-list__item a {
    padding: 30px 0 36px 83px;
  }
  .site-list__item:last-child {
    border: none;
    border-left: 1px solid #ececec;
  }
  .offer-list {
    order: 4;
