    height: 0px;
    display: block;
    content: '';
  }
  .form-item__type,
  .form-item__color,
  .form-item__name {
    font-size: 19px;
    line-height: 18px;
    font-weight: bold;
  }
  .form__list-item--type,
  .form__list-item--color,
  .form__list-item--name {
    flex-direction: row;
  }
  .form__list-item--type {
    width: 335px;
    min-height: 40px;
  }
  .form__list--color {
    width: 521px;
    min-height: 40px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .form__list-item--color {
    flex-wrap: nowrap;
    width: 230px;
  }
  .form input[type="checkbox"]:checked + label,
  .form input[type="checkbox"]:not(:checked) + label,
  .form input[type="radio"]:checked + label,
  .form input[type="radio"]:not(:checked) + label {
    padding-left: 65px;
  }
  .form input[type="radio"]:checked + label:before,
  .form input[type="radio"]:not(:checked) + label:before {
    width: 52px;
    height: 52px;
  }
  .form input[type="radio"]:checked + label::after {
    width: 22px;
    height: 22px;
    top: 17px;
    left: 17px;
  }
  .form input[type="checkbox"]:checked + label:before,
  .form input[type="checkbox"]:not(:checked) + label:before {
    width: 52px;
    height: 52px;
  }
  .form input[type="checkbox"]:checked + label::after {
    width: 33px;
    left: 10px;
    top: 0px;
  }
  .form__list--name {
    flex-grow: 1;
  }
  .form__list-item--name {
    justify-content: flex-start;
    flex-wrap: nowrap;
  }
  .form-item__name {
    padding: 18px 0 15px;
    box-sizing: content-box;
    margin-right: 20px;
  }
  .background-wrapper--form {
    position: relative;
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    background-color: #f1f1f1;
  }
  .background-wrapper--form-footer {
    margin-bottom: 0px;
  }
  .background-wrapper::after {
    content: "";
    position: absolute;
    bottom: -1px;
    display: block;
    width: 100%;
    height: 24px;
    background-image: url(../img/bg-zigzag-fill.svg);
    background-repeat: repeat-x;
    background-position-x: 50%;
    background-size: 768px 25px;
  }
  .form__phone {
    background-color: #f1f1f1;
  }
  .form__mail {
    width: 100%;
    background-color: #f1f1f1;
  }
  .form__submit {
    width: 228px;
  }
  .form__comment {
    order: 2;
  }
  .form-item--type,
  .form-item--color,
  .form-item--name,
  .form-item--phone,
  .form-item--mail,
  .form-item--wishes {
    position: relative;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
  }
  .form-footer {
    position: relative;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-content: flex-end;
  }
  .form__wishes {
    font-size: 19px;
    padding: 17px 15px;
    height: 141px;
    width: 638px;
    resize: none;
    flex-grow: 1;
  }
}
@media (min-width: 1150px) {
  .background-wrapper::after {
    background-size: auto 36px;
  }
  .form__list--color {
    width: 875px;
  }
  .form-footer {
    padding-left: 171px;
  }
}
.modal-window {
  font-family: "Open Sans", "Arial", sans-serif;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 20;
  top: 0;
  background-color: rgba(255, 255, 255, 0.8);
  background-position: 0 0;
  animation: bounce 0.6s;
}
.modal--close {
  display: none;
  animation: bounceOut 0.6s;
}
.modal-window__header {
  font-size: 25px;
  line-height: 30px;
  font-weight: 700;
  text-transform: uppercase;
  width: 150px;
  margin: 0;
  padding-left: 88px;
  text-align: center;
  padding-top: 31px;
}
.modal-window__form {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
  width: 320px;
  position: absolute;
  right: 50%;
  transform: translateX(50%) translateY(-50%);
  top: 50%;
  height: 336px;
  border-top: 1px solid #c7c7c7;
  border-bottom: 1px solid #c7c7c7;
  box-shadow: 0 5px 10px 0 #c7c7c7;
}
.modal-window__description {
  font-size: 15px;
  line-height: 30px;
  color: #444444;
  margin: 0;
  padding-top: 11px;
  padding-bottom: 24px;
  padding-left: 93px;
}
.modal-window__size-block {
  display: flex;
  width: 260px;
  justify-content: space-between;
  margin: 0 auto;
}
.modal-window__radio {
  display: none;
}
.modal-window__radio:checked + .modal-window__label {
  border-color: #63d1bb;
}
.modal-window__label {
  width: 78px;
  height: 48px;
  border: 2px solid #ececec;
  color: #444444;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 17px;
  line-height: 16px;
  font-weight: 700;
}
.modal-window__button {
  margin-top: 31px;
  margin-left: 31px;
  text-decoration: none;
  font-family: "Open Sans", "Arial", sans-serif;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 17px;
  font-weight: 700;
  width: 260px;
  height: 52px;
  border: none;
  background-color: #63d1bb;
  color: white;
  line-height: 16px;
}
.modal-show {
  display: block;
  animation: bounce-down 0.6s;
}
@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes bounceOut {
  0% {
    transform: translateY(0);
  }
  70% {
    transform: translateY(-10px);
  }
  90% {
    transform: translateY(30px);
  }
  100% {
    transform: translateY(-2000px);
  }
}
